@media (max-width: 768px) {
    body{
        background-image: url(https://res.cloudinary.com/drkmgpcad/image/upload/v1726821831/ve1qltzcmjvwqbqcqvcj.png);
        background-position: center;
    }
    header{
 
        font-size: 2rem;
    }


    nav button {
        padding: 5px 15px; 
  
    
    }


    #category-header {

        font-size: 1em;
    }

    #articles-container {
        column-count: 2;
        column-gap: 20px;
        padding: 20px;
    }

    .article {

        padding: 20px;
    }



    .article h2 {
        font-size: 1.2em;
        margin: 15px;
  
    }
    
    .article p {

        font-size: .8em;
        color: black;

    }
 
    .article a {
        font-size: .8rem;
    }
    
    footer p {
        font-size: 60%; 
    }

    #back-to-top {

        height: 8%;
    
    }
    

    .loading-spinner {
        width: 30px;
        height: 30px;

    }
 

}